// generate utility classes
@each $name, $map in $colors {
  $prop: map-get($map, "prop");
  $values: map-get($map, "values");

  @each $k, $v in $values {
    @if($k == "default") {
      .#{$name} {
        #{$prop}: $v;
      }
    } @else {
      .#{$name}-#{$k}{
        #{$prop}: $v;
      }
    }
  }
}

// hover
@each $name, $map in $colors {
  $prop: map-get($map, "prop");
  $values: map-get($map, "values");

  @each $k, $v in $values {
    @if($k == "default") {
      .hover\:#{$name}:hover {
        #{$prop}: $v;
      }
    } @else {
      .hover\:#{$name}-#{$k}:hover {
        #{$prop}: $v;
      }
    }
  }
}

