// breakpoints
// small
@each $name, $map in $colors {
  $prop: map-get($map, "prop");
  $values: map-get($map, "values");

  @each $k, $v in $values {
    @if($k == "default") {
      .sm\:#{$name} {
        @include sm{
          #{$prop}: $v;
        }
      }
    } @else {
      .sm\:#{$name}-#{$k} {
        @include sm {
          #{$prop}: $v;
        }
      }
    }
  }
}

